import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;
const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '▹';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
  .skill-category-header {
    padding-top: 15px;
  }
`;
const StyledPic = styled.div`
  position: relative;
  max-width: 300px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: var(--green);

    &:hover,
    &:focus {
      outline: 0;
      transform: translate(-4px, -4px);

      &:after {
        transform: translate(8px, 8px);
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: var(--navy);
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid var(--green);
      top: 14px;
      left: 14px;
      z-index: -1;
    }
  }
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  const skillSet = [
    {
      category: 'Programming Languages',
      skills: ['Golang', 'Rust', 'Java', 'Node.js + TypeScript'],
    },
    {
      category: 'Frameworks and Libraries',
      skills: ['Spring Boot', 'ExpressJs', 'React', 'Ruby on Rails'],
    },
    {
      category: 'Infrastructure',
      skills: ['DynamoDB', 'Postgres', 'Redis', 'Kafka'],
    },
  ];

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>

      <div className="inner">
        <StyledText>
          <div>
            <p>
              Hello! My name is Christopher, and I'm a passionate software engineer. Originally from
              Malaysia, I came to London back in 2015 for my BSc in Computer Science at{' '}
              <a href="https://www.ucl.ac.uk/" target="_blank">
                University College London
              </a>
              .
            </p>

            <p>
              After graduating, I’ve had the privilege of working at{' '}
              <a href="https://deliveroo.co.uk/" target="_blank">
                one of UK's largest food delivery company
              </a>
              ,{' '}
              <a href="https://www.ocadogroup.com/" target="_blank">
                a logistics company
              </a>
              , and <a href="https://www.capitalone.com/">an American financial corporation</a>.
            </p>

            <p>
              My main focus these days is building scalable and reliable backends for products and
              digital solutions for various companies. Here are a few technologies I’ve been working
              with recently:
            </p>
          </div>

          {skillSet.map(({ category, skills }, i) => (
            <>
              <h4 className="skill-category-header" key={i}>
                {category}
              </h4>
              <ul className="skills-list">
                {skills.map((skill, i) => (
                  <li key={i}>{skill}</li>
                ))}
              </ul>
            </>
          ))}
        </StyledText>

        <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
        </StyledPic>
      </div>
    </StyledAboutSection>
  );
};

export default About;
